import Box from "@mui/material/Box";
import Select from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";

const Selection = (props) => {
  return (
    <Box sx={{ minWidth: 150, maxWidth: 250 }}>
      <FormControl fullWidth variant="filled" size="small">
        <InputLabel id="select-label"></InputLabel>
        <Select
          labelId="select-label"
          id="simple-select"
          defaultValue="SELECT"
          onChange={props.onChange}
        >
          <MenuItem value="SELECT">-- Select --</MenuItem>
          <MenuItem value="LECHE DESN">LECHE DESN</MenuItem>
          <MenuItem value="SMP">SMP</MenuItem>
          <MenuItem value="SMP-ST">SMP-ST</MenuItem>
          <MenuItem value="MP80">MP80</MenuItem>
          <MenuItem value="MP85">MP85</MenuItem>
          <MenuItem value="MCI90">MPI90</MenuItem>
          <MenuItem value="MC85">MC85</MenuItem>
          <MenuItem value="MCI90">MCI90</MenuItem>
          <MenuItem value="WPI90">WPI90</MenuItem>
        </Select>
      </FormControl>
    </Box>
  );
};
export default Selection;
