import React from "react";
import TextField from "@mui/material/TextField";
import InputAdornment from "@mui/material/InputAdornment";

const Imput = (props) => {
  if (props.name === "fat") {
  }
  return (
    <TextField
      onChange={props.onChange}
      id={props.id}
      name={props.name}
      value={props.value}
      label={props.id}
      type="number"
      filled="small"
      size="small"
      margin="dense"
      variant="outlined"
      style={{ width: 150 }}
      InputProps={{
        inputProps: { min: props.min, max: props.max, step: props.step },
        endAdornment: (
          <InputAdornment position="end">{props.unit}</InputAdornment>
        )
      }}
    />
  );
};
export default Imput;
