import React from "react";
import { useEffect, useState } from "react";
import Imput from "./Imput";
import Button from "./Button";
import "./Form.css";
import { styled } from "@mui/material/styles";
import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";
import Radios from "./Radio";
import Selection from "./Select";
import Grid from "@mui/material/Grid";

const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: "left",
  color: theme.palette.text.secondary
}));

const initialValues = {
  fat: "",
  top: "",
  lac: "",
  ash: "",
  temp: ""
};
const source = [
  { id: "SELECT", fat: "", top: "", lac: "", ash: "" },
  { id: "LECHE DESN", fat: 0.06, top: 3.46, lac: 4.94, ash: 0.83 },
  { id: "SMP", fat: 0.15, top: 9.24, lac: 13.33, ash: 2.51 },
  { id: "SMP-ST", fat: 0.14, top: 8.46, lac: 13.73, ash: 2.19 },
  { id: "MP80", fat: 0.15, top: 9.15, lac: 1.2, ash: 0.87 },
  { id: "MP85", fat: 0.22, top: 13.19, lac: 1.13, ash: 0.99 },
  { id: "MPI90", fat: 0.23, top: 13.53, lac: 0.48, ash: 0.62 },
  { id: "MC85", fat: 0.22, top: 11.82, lac: 0.9, ash: 0.65 },
  { id: "MCI90", fat: 0.23, top: 11.75, lac: 0.36, ash: 0.26 },
  { id: "WPI90", fat: 0, top: 47.78, lac: 1.66, ash: 1.37 }
];
const Form = (props) => {
  const [values, setValues] = useState(initialValues);
  const [ts, setTs] = useState("");
  const [top_ts, setTop_ts] = useState("");
  const [dens, setDens] = useState("");

  const selectChange = (e) => {
    if (e.target.value === "0") {
      setValues({ ...values, temp: 10 });
    } else if (e.target.value === "1") {
      setValues({ ...values, temp: 50 });
    }
  };

  useEffect(() => {
    const { fat, top, lac, ash, temp } = values;
    const ts = Number(fat) + Number(top) + Number(lac) + Number(ash);
    setTs(ts.toFixed(1));
    if (ts > 0 && ts < 100) {
      const top_ts = (Number(top) * 100) / Number(ts);
      setTop_ts(top_ts.toFixed(1));
    } else {
      setTop_ts("");
    }
    if (temp > 0 && temp < 100) {
      const waterdensity =
        (999.83952 +
          16.945176 * temp -
          7.9870401 * Math.pow(10, -3) * Math.pow(temp, 2) -
          46.170461 * Math.pow(10, -6) * Math.pow(temp, 3) +
          105.56302 * Math.pow(10, -9) * Math.pow(temp, 4) -
          280.54253 * Math.pow(10, -12) * Math.pow(temp, 5)) /
        (1 + 16.89785 * Math.pow(10, -3) * temp);
      setDens(
        (
          100 /
          (Number(fat) / 920 +
            Number(top) / 1400 +
            Number(lac) / 1650 +
            Number(ash) / 1850 +
            (100 - Number(ts)) / waterdensity)
        ).toFixed(0)
      );
    }
  }, [values]);
  const handleInput = (e) => {
    const { name, value } = e.target;
    setValues({ ...values, [name]: value });
  };
  const masts = (e) => {
    e.preventDefault();
    const inc = 1.01;
    const { fat, top, lac, ash } = values;
    const ts = Number(fat) + Number(top) + Number(lac) + Number(ash) * inc;
    if (ts > 0 && ts < 55) {
      setValues({
        ...values,
        fat: (Number(fat) * inc).toFixed(3),
        top: (Number(top) * inc).toFixed(3),
        lac: (Number(lac) * inc).toFixed(3),
        ash: (Number(ash) * inc).toFixed(3)
      });
    }
  };
  const mastemp = (e) => {
    e.preventDefault();
    setValues({ ...values, temp: Number(values.temp) + 1 });
  };
  const masdens = (e) => {
    e.preventDefault();
    const inc = 1.01;
    const { fat, top, lac, ash } = values;
    const ts = Number(fat) + Number(top) + Number(lac) + Number(ash) * inc;
    if (ts > 0 && ts < 55) {
      setValues({
        ...values,
        fat: (Number(fat) * inc).toFixed(3),
        top: (Number(top) * inc).toFixed(3),
        lac: (Number(lac) * inc).toFixed(3),
        ash: (Number(ash) * inc).toFixed(3)
      });
    }
  };
  const menosts = (e) => {
    e.preventDefault();
    const inc = 0.99;
    const { fat, top, lac, ash } = values;
    const ts = Number(fat) + Number(top) + Number(lac) + Number(ash) * inc;
    if (ts > 0 && ts < 55) {
      setValues({
        ...values,
        fat: (Number(fat) * inc).toFixed(3),
        top: (Number(top) * inc).toFixed(3),
        lac: (Number(lac) * inc).toFixed(3),
        ash: (Number(ash) * inc).toFixed(3)
      });
    }
  };
  const menostemp = (e) => {
    e.preventDefault();
    setValues({ ...values, temp: Number(values.temp) - 1 });
  };
  const menosdens = (e) => {
    e.preventDefault();
    const inc = 0.99;
    const { fat, top, lac, ash } = values;
    const ts = Number(fat) + Number(top) + Number(lac) + Number(ash) * inc;
    if (ts > 0 && ts < 55) {
      setValues({
        ...values,
        fat: (Number(fat) * inc).toFixed(3),
        top: (Number(top) * inc).toFixed(3),
        lac: (Number(lac) * inc).toFixed(3),
        ash: (Number(ash) * inc).toFixed(3)
      });
    }
  };

  function product(e) {
    const nameproduct = e.target.value;
    const selected = source.filter((pname) => pname.id === nameproduct);
    setValues({
      ...values,
      fat: selected[0].fat,
      top: selected[0].top,
      lac: selected[0].lac,
      ash: selected[0].ash
    });
  }
  return (
    <div>
      <Grid
        container
        spacing={0}
        direction="column"
        alignItems="center"
        justifyContent="center"
      >
        <Grid item xs={1}>
          <Selection onChange={product} />
          <Radios onChange={selectChange} />
          <Stack spacing={0.5}>
            <Item
              sx={{
                p: 0.5,
                width: 240,
                "& > :not(style)": { m: 0.5 }
              }}
            >
              <Imput
                id="Fat"
                name="fat"
                unit="%"
                min="0"
                max="100"
                step="0.001"
                value={values.fat}
                onChange={handleInput}
              />
              <br />
              <Imput
                id="TOP"
                name="top"
                unit="%"
                min="0"
                max="100"
                step="0.001"
                value={values.top}
                onChange={handleInput}
              />
              <br />
              <Imput
                id="Lac"
                name="lac"
                unit="%"
                min="0"
                max="100"
                step="0.001"
                value={values.lac}
                onChange={handleInput}
              />
              <br />
              <Imput
                id="Ash"
                name="ash"
                unit="%"
                min="0"
                max="100"
                step="0.001"
                value={values.ash}
                onChange={handleInput}
              />
              <br />
              <Imput
                id="TOP/TS"
                name="top_ts"
                unit="%"
                min="0"
                max="100"
                step="0.1"
                value={top_ts}
                onChange={handleInput}
              />
              <br />
            </Item>
            <Item
              sx={{
                p: 0.5,
                width: 240,
                "& > :not(style)": { m: 0.5 }
              }}
            >
              <Imput
                id="TS"
                name="ts"
                unit="%"
                min="0"
                max="100"
                step="0.1"
                value={ts}
                onChange={handleInput}
              />
              <Button id="botonmas" name="mas" onClick={masts}></Button>
              <Button id="botonmenos" name="menos" onClick={menosts}></Button>

              <br />
              <Imput
                id="Temp."
                name="temp"
                unit="ÂºC"
                min="0"
                max="100"
                step="1"
                value={values.temp}
                onChange={handleInput}
              />
              <Button id="botonmas" name="mas" onClick={mastemp}></Button>
              <Button id="botonmenos" name="menos" onClick={menostemp}></Button>
              <br />
              <Imput
                id="Dens."
                name="dens"
                unit="kg/m3"
                min="980"
                max="1250"
                step="1"
                value={dens}
                onChange={handleInput}
              />
              <Button id="botonmas" name="mas" onClick={masdens}></Button>
              <Button id="botonmenos" name="menos" onClick={menosdens}></Button>
              <div className="firma"> by JMD</div>
            </Item>
          </Stack>
        </Grid>
      </Grid>
    </div>
  );
};
export default Form;
