import React from "react";
import FormControl from "@mui/material/FormControl";
import ArrowDropUpIcon from "@mui/icons-material/ArrowDropUp";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import Fab from "@mui/material/Fab";

const Botonmas = (props) => {
  if (props.name === "mas") {
    return (
      <FormControl>
        <Fab
          style={{
            maxWidth: "30px",
            maxHeight: "30px",
            minWidth: "30px",
            minHeight: "30px"
          }}
          color="primary"
          aria-label="add"
          onClick={props.onClick}
          name={props.name}
          id={props.id}
        >
          <ArrowDropUpIcon />
        </Fab>
      </FormControl>
    );
  }
  if (props.name === "menos") {
    return (
      <FormControl>
        <Fab
          style={{
            maxWidth: "30px",
            maxHeight: "30px",
            minWidth: "30px",
            minHeight: "30px"
          }}
          color="primary"
          aria-label="add"
          onClick={props.onClick}
          name={props.name}
          id={props.id}
        >
          <ArrowDropDownIcon />
        </Fab>
      </FormControl>
    );
  }
};
export default Botonmas;
